import * as layout from "../../routes/$layout.svelte.js";

const components = [
	() => import("../../routes/index.svelte.js"),
	() => import("../../routes/about/index.svelte.js"),
	() => import("../../routes/stuff/index.svelte.js"),
	() => import("../../routes/blog/[slug].svelte.js")
];

const d = decodeURIComponent;
const empty = () => ({});

export const pages = [
	{
		// index.svelte
		pattern: /^\/$/,
		params: empty,
		parts: [components[0]]
	},

	{
		// about/index.svelte
		pattern: /^\/about\/?$/,
		params: empty,
		parts: [components[1]]
	},

	{
		// stuff/index.svelte
		pattern: /^\/stuff\/?$/,
		params: empty,
		parts: [components[2]]
	},

	{
		// blog/[slug].svelte
		pattern: /^\/blog\/([^/]+?)\/?$/,
		params: (m) => ({ slug: d(m[1])}),
		parts: [components[3]]
	}
];

export const ignore = [
	
];

export { layout };